import Sidebar from "@/components/Sidebar";

const topicData = {
  react: {
    video: "https://www.youtube.com/embed/dGcsHMXbSOA",
    pdf: "/cheatsheets/react.pdf",
    image: "/cheatsheets/react.png",
  },
  nodejs: {
    video: "https://www.youtube.com/embed/TlB_eWDSMt4",
    pdf: "/cheatsheets/nodejs.pdf",
    image: "/cheatsheets/nodejs.png",
  },
  mongodb: {
    video: "https://www.youtube.com/embed/OFu2kSmchqY",
    pdf: "/cheatsheets/mongodb.pdf",
    image: "/cheatsheets/mongodb.png",
  },
  expressjs: {
    video: "https://www.youtube.com/embed/L72fhGm1tfE",
    pdf: "/cheatsheets/expressjs.pdf",
    image: "/cheatsheets/expressjs.png",
  },
};

export default function TopicPage({ params }) {
  const topic = params.topic.toLowerCase();
  const data = topicData[topic];

  if (!data) return <p className="text-white">Topic not found</p>;

  return (
    <div className="flex bg-black min-h-screen text-white">
      <Sidebar />
      <main className="flex-1 p-10 space-y-8">
        <h1 className="text-3xl font-bold capitalize">{topic}</h1>

        <section>
          <h2 className="text-xl font-semibold mb-2">Video Tutorial</h2>
          <div className="aspect-w-16 aspect-h-9">
            <iframe
              src={data.video}
              title={topic}
              frameBorder="0"
              allowFullScreen
              className="w-full h-full rounded-lg"
            ></iframe>
          </div>
        </section>

        <section>
          <h2 className="text-xl font-semibold mb-2">Cheatsheet (Image)</h2>
          <img src={data.image} alt={topic} className="rounded-lg border border-gray-700" />
        </section>

        <section>
          <h2 className="text-xl font-semibold mb-2">Cheatsheet (PDF)</h2>
          <a
            href={data.pdf}
            target="_blank"
            className="text-blue-500 underline hover:text-blue-400"
          >
            Open PDF
          </a>
        </section>
      </main>
    </div>
  );
}
